
#search {
  width: 100%;
  background-color: #FFF;
  z-index: 999;
  /*position: relative;*/
  position: absolute;
  top: 0;
}
#search .red {
    color: red;
}
#search .vux-header {
    width: 100%;
    position: fixed;
    top: 0;
    background-color: red;
    z-index: 3;
}
#search .vux-header .vux-header-left .vux-header-back {
      color: #fff;
}
#search .vux-header .vux-header-left .left-arrow:before {
      border: 1px solid #fff;
      border-width: 1px 0 0 1px;
}
#search .vux-header .icon-share3 {
      font-size: 28px;
      position: absolute;
      right: 10px;
}
#search .search-keyword {
    padding: 50px 17px 0 17px;
}
#search .search-keyword .search-input {
      width: 60%;
      padding: 10px 20px;
      border: 1px solid #eee;
}
#search .search-keyword .history {
      color: cornflowerblue;
}
#search .news-wrapper .news-content {
    position: relative;
    z-index: 10;
    background-color: #fff;
}
#search .news-wrapper .news-content .news-item {
      padding: 0.20rem 0.40rem;
      font-size: 0.32rem;
      color: #585151;
      border-bottom: 1px solid #eee;
}
#search .news-wrapper .news-content .news-item .news-title {
        line-height: 0.40rem;
}
#search .news-wrapper .news-content .news-item .img-wrapper {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        margin-top: 0.10rem;
        -webkit-box-pack: justify;
            -ms-flex-pack: justify;
                justify-content: space-between;
}
#search .news-wrapper .news-content .news-item .img-wrapper li {
          width: 2.13rem;
          height: 1.50rem;
          /*flex: 1;*/
}
#search .news-wrapper .news-content .news-item .img-wrapper li img {
            width: 100%;
}
#search .news-wrapper .news-content .news-item .bottom-title {
        font-size: 10px;
        color: #b5b5b5;
        margin-top: 8px;
}
#search .news-wrapper .news-content .news-item .bottom-title .avIcon {
          color: #2a90d7;
          border: 1px solid rgba(42, 144, 215, 0.5);
          border-radius: 2px;
          font-size: 8px;
          padding: 0 2px;
}



