
#my {
  width: 100%;
  text-align: center;
  position: absolute;
  top: 0;
  min-height: 100%;
}
#my .avatar-wrap {
    width: 100%;
    height: 200px;
    background-color: rgba(7, 17, 27, 0.3);
}
#my .avatar-wrap .background {
      width: 100%;
      height: 200px;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      z-index: -1;
      background-image: url(https://sfault-avatar.b0.upaiyun.com/170/530/1705302713-5874fbd7ed519_huge256);
      background-size: 100% 100%;
      background-repeat: no-repeat;
      -webkit-filter: blur(10px);
              filter: blur(10px);
}
#my .avatar-wrap .avatar-img {
      width: 100px;
      margin: 0 auto;
}
#my .avatar-wrap .avatar-img img {
        width: 100%;
        border-radius: 50%;
        margin-top: 50px;
}
#my .avatar-wrap .avatar-img .title {
        font-size: 20px;
        color: #1aea7c;
        margin-top: 8px;
}
#my .connect-me {
    margin: 20px 20px;
}
#my .connect-me li {
      padding: 10px 5px;
      border-top: 1px solid #eee;
      border-bottom: 1px solid #eee;
      margin: 10px 0;
      text-align: left;
      font-size: 16px;
}
#my .connect-me li a {
        display: block;
        width: 100%;
}
#my .connect-me li a .iconfont {
          padding-right: 5px;
}
#my .connect-me li a .right {
          float: right;
}



