
body, html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif;
}
.clearfix {
  display: inline-block;
  zoom: 1;
}
.clearfix:after {
    content: " ";
    display: block;
    height: 0;
    line-height: 0;
    clear: both;
    visibility: hidden;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px:before {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px:before {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
.tabwrap {
  width: 100%;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 45px;
  padding-top: 5px;
  z-index: 99;
}
.tabwrap:before {
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    border-top: 1px solid #eee;
    content: ' ';
}
.tabwrap .tab-item {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    text-align: center;
}
.tabwrap .tab-item .icon {
      width: 0.48rem;
      height: 0.48rem;
      background-size: 100% auto;
      background-repeat: no-repeat;
      margin: 0 auto;
}
.tabwrap .tab-item .icon.icon1 {
        background-image: url(../assets/demo/icon11.png);
}
.tabwrap .tab-item .icon.icon2 {
        background-image: url(../assets/demo/icon21.png);
}
.tabwrap .tab-item .icon.icon3 {
        background-image: url(../assets/demo/icon31.png);
}
.tabwrap .tab-item .icon.icon4 {
        background-image: url(../assets/demo/icon41.png);
}
.tabwrap .tab-item .icon-word {
      font-size: 12px;
      margin-top: 5px;
}
.tabwrap .tab-item .active .icon1 {
      background-image: url(../assets/demo/icon12.png);
}
.tabwrap .tab-item .active .icon2 {
      background-image: url(../assets/demo/icon22.png);
}
.tabwrap .tab-item .active .icon3 {
      background-image: url(../assets/demo/icon32.png);
}
.tabwrap .tab-item .active .icon4 {
      background-image: url(../assets/demo/icon42.png);
}
.tabwrap .tab-item .active .icon-word {
      color: red;
}



