
#collection {
  width: 100%;
  position: absolute;
  min-height: 100%;
}
#collection .nodata {
    text-align: center;
    color: #ccc;
    font-size: 28px;
    margin-top: 200px;
    line-height: 40px;
}
#collection .news-content {
    z-index: 10;
    background-color: #fff;
    margin-bottom: 50px;
}
#collection .news-content .news-item {
      padding: 0.20rem 0.40rem;
      font-size: 0.32rem;
      color: #585151;
      border-bottom: 1px solid #eee;
}
#collection .news-content .news-item .news-title {
        line-height: 0.40rem;
}
#collection .news-content .news-item .img-wrapper {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        margin-top: 0.10rem;
        -webkit-box-pack: justify;
            -ms-flex-pack: justify;
                justify-content: space-between;
}
#collection .news-content .news-item .img-wrapper li {
          width: 2.13rem;
          height: 1.50rem;
          /*flex: 1;*/
}
#collection .news-content .news-item .img-wrapper li img {
            width: 100%;
}
#collection .news-content .news-item .bottom-title {
        font-size: 10px;
        color: #b5b5b5;
        margin-top: 8px;
}
#collection .news-content .news-item .bottom-title .avIcon {
          color: #2a90d7;
          border: 1px solid rgba(42, 144, 215, 0.5);
          border-radius: 2px;
          font-size: 8px;
          padding: 0 2px;
}



