
body, html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif;
}
.clearfix {
  display: inline-block;
  zoom: 1;
}
.clearfix:after {
    content: " ";
    display: block;
    height: 0;
    line-height: 0;
    clear: both;
    visibility: hidden;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px:before {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px:before {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
#app {
  height: 100%;
}
#app .tabwrap {
    width: 100%;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 45px;
    padding-top: 5px;
    z-index: 99;
}
#app .tabwrap:before {
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      border-top: 1px solid #eee;
      content: ' ';
}
#app .tabwrap .tab-item {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      text-align: center;
}
#app .tabwrap .tab-item .icon {
        width: 0.48rem;
        height: 0.48rem;
        background-size: 100% auto;
        background-repeat: no-repeat;
        margin: 0 auto;
}
#app .tabwrap .tab-item .icon.icon1 {
          background-image: url(./assets/demo/icon11.png);
}
#app .tabwrap .tab-item .icon.icon2 {
          background-image: url(./assets/demo/icon21.png);
}
#app .tabwrap .tab-item .icon.icon3 {
          background-image: url(./assets/demo/icon31.png);
}
#app .tabwrap .tab-item .icon.icon4 {
          background-image: url(./assets/demo/icon41.png);
}
#app .tabwrap .tab-item .icon-word {
        font-size: 12px;
        margin-top: 5px;
}
#app .tabwrap .tab-item .active .icon1 {
        background-image: url(./assets/demo/icon12.png);
}
#app .tabwrap .tab-item .active .icon2 {
        background-image: url(./assets/demo/icon22.png);
}
#app .tabwrap .tab-item .active .icon3 {
        background-image: url(./assets/demo/icon32.png);
}
#app .tabwrap .tab-item .active .icon4 {
        background-image: url(./assets/demo/icon42.png);
}
#app .tabwrap .tab-item .active .icon-word {
        color: red;
}
#app .fade-enter-active, #app .fade-leave-active {
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
#app .fade-enter, #app .fade-leave-to {
    opacity: 0;
}
#app .forward-enter-active, #app .forward-leave-active {
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
#app .forward-enter {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
#app .forward-leave-to {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
#app .reverse-enter-active, #app .reverse-leave-active {
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
#app .reverse-enter {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
#app .reverse-leave-to {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}



