
@charset "UTF-8";
#index {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 50px;
  min-height: 100%;
  /*测试*/
}
#index .header-x {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 40px;
    background-color: red;
    z-index: 999;
    text-align: center;
}
#index .header-x .title {
      line-height: 40px;
      color: #fff;
      font-size: 18px;
}
#index .icon-search1 {
    position: absolute;
    top: 4px;
    right: 12px;
    font-size: 30px;
    color: #fff;
}
#index .icon-mail_fill {
    position: absolute;
    top: 4px;
    left: 12px;
    font-size: 30px;
    color: #fff;
}
#index .tab-title {
    position: fixed;
    left: 0;
    right: 0;
    top: 40px;
    z-index: 999;
    background-color: #eee;
    overflow: hidden;
}
#index .tab-title .box1 {
      /*display: flex;*/
      overflow: hidden;
      background-color: #f4f5f6;
      width: 11.0rem;
      position: relative;
}
#index .tab-title .box1 .box1-item {
        font-size: 0.28rem;
        text-align: center;
        width: 1.0rem;
        height: 0.70rem;
        line-height: 0.70rem;
        float: left;
}
#index .tab-title .box1 .box1-item.active {
          color: red;
}
#index .news-wrapper {
    position: absolute;
    left: 0;
    right: 0;
    width: 100%;
    top: 1.62rem;
    bottom: 0;
    background-color: #fff;
    overflow: hidden;
}
#index .news-wrapper .news-content {
      position: relative;
      z-index: 10;
      background-color: #fff;
}
#index .news-wrapper .news-content .news-item {
        padding: 0.20rem 0.40rem;
        font-size: 0.32rem;
        color: #585151;
        border-bottom: 1px solid #eee;
}
#index .news-wrapper .news-content .news-item .news-title {
          line-height: 0.40rem;
}
#index .news-wrapper .news-content .news-item .img-wrapper {
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          margin-top: 0.10rem;
          -webkit-box-pack: justify;
              -ms-flex-pack: justify;
                  justify-content: space-between;
}
#index .news-wrapper .news-content .news-item .img-wrapper li {
            width: 2.13rem;
            height: 1.50rem;
            /*flex: 1;*/
}
#index .news-wrapper .news-content .news-item .img-wrapper li img {
              width: 100%;
}
#index .news-wrapper .news-content .news-item .bottom-title {
          font-size: 10px;
          color: #b5b5b5;
          margin-top: 8px;
}
#index .news-wrapper .news-content .news-item .bottom-title .avIcon {
            color: #2a90d7;
            border: 1px solid rgba(42, 144, 215, 0.5);
            border-radius: 2px;
            font-size: 8px;
            padding: 0 2px;
}
#index .loading {
    /*position:absolute;*/
    /*left:0;*/
    width: 100%;
    text-align: center;
    color: green;
    font-size: 18px;
}
#index .loading img {
      width: 100px;
}
#index .load-result {
    position: absolute;
    left: 0;
    right: 0;
    top: 30px;
    text-align: center;
    height: 30px;
    width: 80px;
    background-color: #000;
    color: #fff;
    border-radius: 5px;
    z-index: 999;
    font-size: 14px;
    line-height: 30px;
    margin: 0 auto;
}
#index .loading2 {
    text-align: center;
    padding: 10px;
}
#index .fade-enter-active, #index .fade-leave-active {
    -webkit-transition: all 3s;
    transition: all 3s;
}
#index .fade-enter, #index .fade-leave-to {
    opacity: 0;
}
#index .ttt {
    position: fixed;
    left: 0.5rem;
    bottom: 2rem;
    z-index: 1000;
    height: 0.9rem;
    width: 0.9rem;
    border-radius: 50%;
    background: #d43d3d;
    text-align: center;
    line-height: 0.9rem;
}
#index .ttt.fade1-enter-active, #index .ttt.fade1-leave-active {
      -webkit-transition: all 5s;
      transition: all 5s;
}
#index .ttt.fade1-enter, #index .ttt.fade1-leave-to {
      opacity: 0;
}



